.header {
  height: 6rem;
  background-image: linear-gradient(315deg, #2234ae 0%, #191714 74%);
  position: relative;
}

.logo {
  color: white;
  font-family: "Lato", sans-serif;
  font-weight: 700;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-30%, -50%);
}

.logo--big {
  font-size: 2.7rem;
}
.logo--big::after {
  content: "";
  background-color: white;
  height: 3rem;
  width: 3rem;
  display: inline-block;
  border-radius: 10rem;
  position: absolute;
  left: -3.6rem;
  top: 50%;
  transform: translateY(-50%);
}

.nav-container {
  background-color: white;
  padding: 2rem 0 1.5rem 0;
}

.nav-container--fixed {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 2;
}

.nav {
  width: 30rem;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  /* justify-content: space-between; */
}

