.nav__next-page {
  width: 1.2rem;
  height: 1.2rem;
  border: solid black;
  border-width: 0 0 0.32rem 0.32rem;
} */

.nav__goto-input {
  width: 16rem;
  height: 3rem;
  border: 0.3rem solid black;
  border-radius: 0.5rem 0 0 0.5rem;
  padding: 0 0.5rem;
}

.nav__goto-btn {
  display: inline-block;
  height: 3rem;
  width: 3rem;
  border: solid black;
  border-width: 0.3rem 0.3rem 0.3rem 0;
  border-radius: 0 0.5rem 0.5rem 0;
}
.nav__goto-btn img {
  height: 1.6rem;
  margin: 0.5rem 0 0 0.5rem;
}

.category__item,
.sections__item,
.people__item,
.load-btn {
  display: block;
  text-decoration: none;
  color: white;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
  letter-spacing: 0.1rem;
  text-transform: capitalize;
  border-radius: 5px;
  background-image: linear-gradient(
    315deg,
    rgba(3, 229, 183, 0.6) 0%,
    rgba(3, 122, 222, 0.6) 74%
  );
  width: 20rem;
  padding: 1.5rem;
  margin: 4.5rem auto 0 auto;
  cursor: pointer;
}

.heading {
  margin: 10rem 0 5rem 0;
  font-size: 2.4rem;
  text-align: center;
}

.quote-box-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3rem;
  margin-top: 3rem;
}

.quote-box {
  width: 30rem;
  padding: 3rem 2rem 6rem;
  background-color: #c5d1f6;
  position: relative;
  border-radius: 5px;
}

.quote-box__serial {
  display: inline-block;
  position: absolute;
  top: 0;
  left: -2rem;
  border-radius: 5px;
  padding: 1rem;
  text-align: center;
  background-color: #c5d1f6;
}

.quote-box__quote {
  font-size: 1.7rem;
  text-align: center;
}

.quote-box__mentor {
  font-size: 1.2rem;
  position: absolute;
  bottom: 1rem;
  right: 2rem;
}

.quote-box__section {
  display: none;
  font-size: 1.2rem;
  position: absolute;
  background-color: white;
  padding: 1rem;
  bottom: 0;
  left: 0;
}

.footer {
  height: 7rem;
  background-color: #001233;
  position: relative;
  margin-top: 10rem;
}

.footer__copyright {
  color: white;
  font-size: 1.2rem;
  font-family: monospace;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.footer__dev {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

.footer__dev:hover {
  text-decoration: underline;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

html {
  font-size: 62.5%;
}

/* .nav__prev-page {
  transform: rotate(45deg);
}

.nav__next-page {
  transform: rotate(-135deg);
} */

.nav__goto {
  display: flex;
  align-items: center;
}

@media only screen and (min-width: 481px) {
  html {
    font-size: 65%;
  }
  .nav,
  .quote-box {
    width: 35rem;
  }
  .todays-quotes,
  .people-quotes {
    gap: 4rem;
  }
}

@media only screen and (min-width: 769px) {
  html {
    font-size: 68%;
  }
  .nav,
  .quote-box {
    width: 45rem;
  }
}
